@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

:root {
  --primary-font-color: white;
  --header-h1-color: #dc143c;
  --header-background-color: black;
  --submit-hover-background-color: rgb(180, 22, 54);
  --background-color: #dc143c;
}

body {
  margin: 0;
  padding: 0;
  background: url("https://images.unsplash.com/photo-1489599849927-2ee91cede3ba?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1950&q=80");
  background-color: black;
  background-position: 50% 30%;
  background-repeat: no-repeat;
}


.header {
 width: 100%;
 position: fixed;
 z-index: 500;
 background: rgb(0,0,0);
background: linear-gradient(356deg, rgba(0,0,0,1) 69%, rgba(220,20,60,1) 98%);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
padding: 5px 0 20px 0px;
}

.header-h1 {
  font-family: "Lobster", cursive;
  font-size: 5.5rem;
  margin-top: 0;
  margin-bottom: 15px;
  color: #dc143c;
  color: var(--header-h1-color);
  text-shadow: 2px 0px 2px #dc143c;
}

.search {
  width: 280px;
  padding: 8px;
  border: none;
  margin-right: 15px;
  font-size: 1.1rem;
}

.submit {
  padding: 8px 20px;
  border: 1px solid #dc143c;
  background-color: #dc143c;
  background-color: var(--background-color);
  color: white;
  color: var(--primary-font-color);
  font-size: 1.1rem;
  font-weight: bolder;
  cursor: pointer;
  border-radius: 5px;
}

.submit:hover {
  background-color: rgb(180, 22, 54);
  background-color: var(--submit-hover-background-color);
  border: 1px solid rgb(180, 22, 54);
}

.movie-container {
  position: absolute;
  top: 250px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}


@media (max-width: 500px) {
  .header-h1 {
    font-size: 3.5rem;
  }

  .search {
    width: 200px;
    padding: 5px;
    font-size: 1rem;
  }

  .submit {
    padding: 5px 10px;
    border: 1px solid #dc143c;
    font-size: 1rem;
  }
  .movie-container {
    top: 200px;
  }
}

:root {
  --primary-font-color: white;
  --text-background-color: #dc143c;
}

.movie {
  position: relative;
  width: 320px;
  background-color: white;
  margin: 1.5rem;
  overflow: hidden;
  border-radius: 6px;
  border: 1px solid white;
  box-shadow: 3px 3px 5px rgba(255, 255, 255, 0.6);
  cursor: pointer;
}

.movie img {
  width: 100%;
  height: 500px;
  object-fit: cover;
}

.flip-box {
  background-color: transparent;
  width: 320px;
  height: 500px;
  border: 1px solid white;
  -webkit-perspective: 1000px;
          perspective: 1000px;
}

.flip-box-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: -webkit-transform 1.2s;
  transition: transform 1.2s;
  transition: transform 1.2s, -webkit-transform 1.2s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.flip-box:hover .flip-box-inner {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.flip-box-front,
.flip-box-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.flip-box-back {
  position: relative;
  background-color: rgb(13, 29, 39);
  color: white;
  color: var(--primary-font-color);
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  padding: 15px;
  font-size: 1rem;
  text-align: start;
  position: relative;
  overflow: auto;
}

.vote_average {
  position: absolute;
  text-align: center;
  top: 8px;
  right: 8px;
  width: 40px;
  height: 40px;
  line-height: 40px;
  background-color: #dc143c;
  background-color: var(--text-background-color);
  font-size: 1.4rem;
  font-weight: bolder;
  border-radius: 50%;
  
}

.overview-p {
  margin-bottom: 30px;
}

.release_date {
  background-color: #dc143c;
  background-color: var(--text-background-color);
  padding: 5px 8px;
  border-radius: 4px;
}


@media (max-width: 500px) {
  .movie {
    width: 280px;
  }
  .flip-box {
    width: 280px;
   
  }
}

